.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #A18CD1 0%,#FBC2EB 100%, #fad0c4 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.recipe-headings {
  font-size: 25px;
}

.ingredients {
  font-size: 14px;
}

.main-heading {
  text-align: center;
  padding-top: 30px;
  color: #FF0000;
}